	} from '@/stores/video.js'

	// ‰∏ãÊãâÂà∑Êñ∞Èí©Â≠ê
	onPullDownRefresh(async () => {
		console.log('‰∏ãÊãâÂà∑Êñ∞Ëß¶Âèë')
		// ÊâßË°åÂà∑Êñ∞Êï∞ÊçÆÁöÑÂáΩÊï∞
		await fetchVideoList();
		// ÂÅúÊ≠¢‰∏ãÊãâÂà∑Êñ∞
		uni.stopPullDownRefresh()

	})
	// Ëé∑ÂèñËßÜÈ¢ëÂàóË°®ÁöÑÂáΩÊï∞(È°µÈù¢ÂºÄÂßãÂä†ËΩΩÁöÑÊï∞ÊçÆ)
	const fetchVideoList = async () => {
		try {
			const videoinfo = {
				page: 1,
				limit: 10
			};
			const Videoinfo = await apiGetVideo(videoinfo);
			console.log('API ËøîÂõûÊï∞ÊçÆ:', Videoinfo);
			if (Videoinfo.data && Videoinfo.data.records && Array.isArray(Videoinfo.data.records)) {
				videoList.value = Videoinfo.data.records.map(item => ({
					title: item.title,
					src: "http://video.caimizm.com/" + item.url,
					id: item.id,
					account: item.account,
					likeCount: item.likeCount,
					createTime: item.createTime,
					flag: item.price > 0 ? item.flag : false,
					price: item.price,
					updateTime: item.updateTime,
					imgurl: "http://video.caimizm.com/" + item.vimg
				}));
				console.log('Êõ¥Êñ∞ÂêéÁöÑ videoList:', videoList.value);
			} else {
				console.warn('API ËøîÂõûÊï∞ÊçÆÊ†ºÂºè‰∏çÁ¨¶ÂêàÈ¢ÑÊúü:', Videoinfo);
			}
		} catch (error) {
			console.error('Ëé∑ÂèñËßÜÈ¢ëÂ§±Ë¥•:', error);
			uni.showToast({
				title: 'Ëé∑ÂèñËßÜÈ¢ëÂ§±Ë¥•',
				icon: 'none'
			});
		}
	}


	// ÂàùÂßãÂåñ store
	const videoStore = useVideoStore()
	// ‰∏ãÊãâÈÄâÊã©Âô®Áõ∏ÂÖ≥Êï∞ÊçÆ
	const pickerOptions = ref(['ÊéíÂàó‰∫î', 'ÊéíÂàó‰∏â', '‰∏ÉÊòüÂΩ©', 'Á¶èÂΩ©3D']);
	const pickerIndex = ref(0);

	// ÂìçÂ∫îÂºèÊï∞ÊçÆ
	const currentTab = ref('plw');
	const upcomingTab = ref('plw');
	const currentNav = ref('home');
	const plwNumbers = ref(['8', '6', '8', '5', '7']);
	const qxcNumbers = ref(['2', '0', '4', '9', '3', '8', '8']);
	const plwUpcomingIssue = ref('25214Êúü');
	const qxcUpcomingIssue = ref('3225Êúü');
	const upcomingAction = ref('follow');

	// ËßÜÈ¢ëÂàóË°®Êï∞ÊçÆ
	const videoList = ref([]);
	//ÁÇπËµûÂàóË°®Êï∞ÊçÆ
	const likeList = ref([]);

	// ‰∏ãÊãâÈÄâÊã©Âô®ÂèòÂåñ‰∫ã‰ª∂
	const onPickerChange = (e) => {
		const index = e.detail.value;
		pickerIndex.value = index;

		// Ê†πÊçÆÁ¥¢ÂºïËÆæÁΩÆÂΩìÂâçÊ†áÁ≠æ
		switch (index) {
			case 0:
				currentTab.value = 'plw';
				break;
			case 1:
				currentTab.value = 'pls';
				break;
			case 2:
				currentTab.value = 'qxc';
				break;
			case 3:
				currentTab.value = 'fc';
				break;
		}
	};

	// ÊñπÊ≥ï
	const switchTab = (tab) => {
		currentTab.value = tab;
	};

	const switchUpcomingTab = (tab) => {
		upcomingTab.value = tab;
	};

	const switchUpcomingAction = (action) => {
		upcomingAction.value = action;
		console.log('ÂàáÊç¢Âà∞:', action);
	};

	const handleSwiperChange = (e) => {
		currentTab.value = e.detail.current === 0 ? 'plw' : 'qxc';
	};

	const switchNav = (nav) => {
		currentNav.value = nav;
	};


	// Êí≠ÊîæËßÜÈ¢ëÊñπÊ≥ï - Êñ∞Â¢û‰ªòË¥πÊ£ÄÊü•
	const playVideo = async (video) => {
		// Ê£ÄÊü•ÊòØÂê¶ÁôªÂΩï
		const token = getToken();
		if (!token) {
			uni.showToast({
				title: 'ËØ∑ÂÖàÁôªÂΩï',
				icon: 'none'
			});
			setTimeout(() => {
				uni.navigateTo({
					url: '/pages/login/login'
				});
			}, 1500);
			return;
		}

		// Â∞ÜÂΩìÂâçËßÜÈ¢ë‰øùÂ≠òÂà∞ Pinia store
		videoStore.setCurrentVideo(video)

		// Â¶ÇÊûúËßÜÈ¢ëÊòØÂÖçË¥πÁöÑÔºàprice‰∏∫0Êàñflag‰∏∫falseÔºâÔºåÁõ¥Êé•Êí≠Êîæ
		if (!video.flag || video.price === 0) {
			// ÂÖçË¥πËßÜÈ¢ëÁõ¥Êé•Êí≠Êîæ
			uni.navigateTo({
				url: `/pages/video/play?id=${video.id}`
			});
			return;
		}

		// Ê£ÄÊü•ËßÜÈ¢ëÊòØÂê¶Êî∂Ë¥π
		try {
			// Êü•ËØ¢Áî®Êà∑ÊòØÂê¶Â∑≤‰ªòË¥π
			const paymentCheck = await apiCheckVideoPayment({
				videoId: video.id,
				account: getAccount()
			});

			if (paymentCheck.data) {
				if (paymentCheck.data) {
					// Áî®Êà∑Â∑≤‰ªòË¥πÔºåÁõ¥Êé•Êí≠Êîæ
					uni.navigateTo({
						url: `/pages/video/play?id=${video.id}`
					});
				} else {
					// Áî®Êà∑Êú™‰ªòË¥πÔºåÊòæÁ§∫‰ªòË¥πÊèêÁ§∫
					uni.showModal({
						title: '‰ªòË¥πËßÜÈ¢ë',
						content: `ËßÇÁúãÊ≠§ËßÜÈ¢ëÈúÄË¶ÅÊîØ‰ªò${video.price}ÈáëÂ∏Å`,
						confirmText: 'Á´ãÂç≥ÊîØ‰ªò',
						cancelText: 'ÂèñÊ∂à',
						success: async (res) => {
							if (res.confirm) {
								// ËøôÈáåË∞ÉÁî®ÊîØ‰ªòÊé•Âè£
								await payForVideo(video);
							}
						}
					});
				}
			} else {
				uni.navigateTo({
					url: `/pages/video/play?id=${video.id}`
				});
			}
		} catch (error) {
			uni.showToast({
				title: error.message || 'Êü•ËØ¢Â§±Ë¥•',
				icon: 'none'
			});
		}
	};

	// ÊîØ‰ªòËßÜÈ¢ëÊñπÊ≥ï
	const payForVideo = async (video) => {
		try {
			// ËøôÈáåË∞ÉÁî®ÊîØ‰ªòÊé•Âè£
			// const paymentResult = await apiPayForVideo({...});

			// ÊîØ‰ªòÊàêÂäüÂêéÊõ¥Êñ∞ËßÜÈ¢ëÁä∂ÊÄÅ
			video.hasPaid = true;

			uni.showToast({
				title: 'ÊîØ‰ªòÊàêÂäüÔºåÂºÄÂßãÊí≠Êîæ',
				icon: 'success'
			});

			// Êí≠ÊîæËßÜÈ¢ë
			uni.navigateTo({
				url: `/pages/video/play?id=${video.id}`
			});
		} catch (error) {
			uni.showToast({
				title: 'ÊîØ‰ªòÂ§±Ë¥•',
				icon: 'none'
			});
		}
	};

	// Ê£ÄÊü•ËßÜÈ¢ë‰ªòË¥πÁä∂ÊÄÅ
	const checkVideoPaymentStatus = async () => {
		try {
			const account = getAccount();
			if (!account) return;

			// ÊâπÈáèÊ£ÄÊü•ËßÜÈ¢ë‰ªòË¥πÁä∂ÊÄÅ
			const videoIds = videoList.value.map(video => video.id).filter(id => id);
			if (videoIds.length === 0) return;

			const paymentStatus = await apiCheckVideoPayment({
				videoIds: videoIds.join(','),
				account: account
			});

			if (paymentStatus.success) {
				// Êõ¥Êñ∞ËßÜÈ¢ë‰ªòË¥πÁä∂ÊÄÅ
				videoList.value.forEach(video => {
					const paidVideo = paymentStatus.data.find(item => item.videoId === video.id);
					if (paidVideo) {
						video.hasPaid = paidVideo.hasPaid;
					}
				});
			}
		} catch (error) {
			console.error('Ê£ÄÊü•ËßÜÈ¢ë‰ªòË¥πÁä∂ÊÄÅÂ§±Ë¥•:', error);
		}
	};

	//ÊòØÂê¶ÁÇπËµû
	// ÁÇπËµûÂäüËÉΩ
	const toggleLike = async (video) => {
		try {
			// ‰øùÂ≠òÂéüÂßãÁä∂ÊÄÅÔºå‰ª•‰æøÂú®ËØ∑Ê±ÇÂ§±Ë¥•Êó∂ÊÅ¢Â§ç
			const originalIsLiked = video.isLiked;
			const originalLikeCount = video.likeCount;
			// console.log(originalLikeCount)

			// Á´ãÂç≥Êõ¥Êñ∞UIÔºåÊèê‰æõÊõ¥Â•ΩÁöÑÁî®Êà∑‰ΩìÈ™å
			video.isLiked = !video.isLiked;
			video.likeCount = video.isLiked ? video.likeCount + 1 : video.likeCount - 1;

			// Ë∞ÉÁî®ÁÇπËµûAPI
			console.log(video, "====ËøáÊù•ÁöÑÊï∞ÊçÆ=====")
			const response = await apiGetIsLike(video);
			console.log('ÁÇπËµûÊìç‰ΩúÊàêÂäü');

			const a = await apiGetLikelist(getAccount());

			console.log(a);
		} catch (error) {
			console.error('ÁÇπËµûÊìç‰ΩúÂ§±Ë¥•:', error);

			// ÊÅ¢Â§çÂéüÂßãÁä∂ÊÄÅ
			video.isLiked = originalIsLiked;
			video.likeCount = originalLikeCount;

			uni.showToast({
				title: 'Êìç‰ΩúÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
				icon: 'none'
			});
		}
	};


